import { getImagesByQuery } from './js/pixabay-api';
import {
  createGallery,
  clearGallery,
  showLoader,
  hideLoader,
  showLoadMoreButton,
  hideLoadMoreButton,
} from './js/render-functions';
import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';

const form = document.querySelector('#searchForm');
const loadMoreBtn = document.getElementById('loadMoreBtn');
const gallery = document.querySelector('.gallery');

let currentPage = 1;
let currentQuery = '';

form.addEventListener('submit', async e => {
  e.preventDefault();
  const query = form.elements['search-text'].value.trim();

  if (!query) {
    iziToast.error({
      title: 'Error',
      message: 'Please enter a search term!',
    });
    return;
  }

  currentQuery = query;
  currentPage = 1;
  clearGallery();
  showLoader();

  try {
    const data = await getImagesByQuery(query, currentPage, 15);
    if (data.hits.length === 0) {
      iziToast.warning({
        title: 'No results',
        message:
          'Sorry, there are no images matching your search query. Please try again!',
      });
      return;
    }

    createGallery(data.hits);

    // ĞŸĞ¾ĞºĞ°Ğ·ÑƒÑ”Ğ¼Ğ¾ ĞºĞ½Ğ¾Ğ¿ĞºÑƒ Load more, ÑĞºÑ‰Ğ¾ Ñ” Ğ±Ñ–Ğ»ÑŒÑˆĞµ Ğ·Ğ¾Ğ±Ñ€Ğ°Ğ¶ĞµĞ½ÑŒ
    if (data.hits.length > 0 && currentPage * 15 < data.totalHits) {
      showLoadMoreButton();
    } else {
      hideLoadMoreButton();
    }
  } catch (err) {
    iziToast.error({
      title: 'Error',
      message: 'Something went wrong. Try again!',
    });
  } finally {
    hideLoader();
  }
});

// Ğ¤ÑƒĞ½ĞºÑ†Ñ–Ñ Ğ´Ğ»Ñ Ğ·Ğ°Ğ²Ğ°Ğ½Ñ‚Ğ°Ğ¶ĞµĞ½Ğ½Ñ Ğ´Ğ¾Ğ´Ğ°Ñ‚ĞºĞ¾Ğ²Ğ¸Ñ… Ğ·Ğ¾Ğ±Ñ€Ğ°Ğ¶ĞµĞ½ÑŒ
async function loadMoreImages() {
  if (!currentQuery) return;

  showLoader();
  try {
    const data = await getImagesByQuery(currentQuery, currentPage + 1, 15);

    if (data.hits.length === 0) {
      iziToast.warning({
        title: 'No more images',
        message: 'There are no more images to load.',
      });
      hideLoader();
      hideLoadMoreButton();
      return;
    }

    createGallery(data.hits);
    currentPage++;
    hideLoader();
  } catch (err) {
    iziToast.error({
      title: 'Error',
      message: 'Something went wrong. Try again!',
    });
    hideLoader();
  }
}

// ĞĞ±Ñ€Ğ¾Ğ±Ğ½Ğ¸Ğº Ğ¿Ğ¾Ğ´Ñ–Ñ— Ğ´Ğ»Ñ ĞºĞ½Ğ¾Ğ¿ĞºĞ¸ "Load more"
loadMoreBtn.addEventListener('click', loadMoreImages);
